body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

.icons-container {
	/* width: 100%; */
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin: 50px;
}

.icons-container .icon-wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	padding: 10px;
  -ms-flex: 1 1 200px;
      flex: 1 1 200px;
  margin: 10px 0;
}

.icons-container .icon-wrapper span.icon-name {
	background-color: #f6f8fa;
	color: #929292;
	padding: 3px 7px;
	margin: 15px 0 0 0;
	border-radius: 3px;
	letter-spacing: 0.3px;
	font-family: monospace;
	font-size: 13px;
}



